






















































/*返回键*/
.back{
	font-size: 30px;
	color: white;
	position: absolute;
	line-height: 42px;
	left: 10px;
}
.intro{
	position: absolute;
	width: 100%;
	top: 42px;
	z-index: 100;
	background: #e0e0e0;
}
.intro-h,.intro-c{
	width: 100%;
	position: relative;
}
.intro .intro-h-icon1{
	display: inline-block;
	width: 100%;
}
.intro-h>a{
	display: inline-block;
	width: 30px;
	height: 30px;
	position: absolute;
	top: 10px;
}
.intro-h-icon2{
	background: url("../img/allIcon.png") no-repeat;
	background-position: 17% 99%;
	background-size: 300px;
	right: 70px;
}
.intro-h-icon3{
	background: url("../img/allIcon.png") no-repeat;
	background-position: 35% 99%;
	background-size: 250px;
	right: 30px;
}
.c-main{
	width: 100%;
	padding: 10px;
	position: relative;
	box-sizing: border-box;
	background: white;
}
.c-main>li:nth-child(1){
	font-size: 16px;
	color: black;
	line-height: 20px;
}
.c-main>li:nth-child(2){
	font-size: 14px;
	color: #666666;
	line-height: 20px;
	border-bottom:1px solid #e0e0e0;
	display: -ms-flexbox;
	display: flex;
	margin-top: 20px;
	padding-bottom: 5px;
}
.c-main>li:nth-child(3){
	font-size: 14px;
	color: #666666;
	line-height: 20px;
	display: -ms-flexbox;
	display: flex;
	padding-top: 5px;
}
.c-main>li:nth-child(2)>span{
	-ms-flex: 1;
	    flex: 1;
}
.c-main>li:nth-child(3)>span{
	-ms-flex: 1;
	    flex: 1;
}
.c-section{
	width: 100%;
	margin-top: 10px;
	display: -ms-flexbox;
	display: flex;
	border-bottom:1px solid #e0e0e0;
}
.c-section>li{
	-ms-flex: 1;
	    flex: 1;
	height: 42px;
	text-align: center;
	background: white;

	line-height: 42px;
	font-size: 16px;
	position: relative;
	color: #666666;
}
.line{
	border-bottom: 2px solid #ff3f12;
	margin-top: -2px;
	box-shadow: 0px 0px 2px 2px #D2D3D5;
}
.details-ul,.evaluate-ul{
	width: 100%;
	padding-bottom: 42px;
}
.details-ul>li,.evaluate-ul>li,.eval-c{
	width: 100%;
	padding: 5px;
	box-sizing: border-box;
}
.details-ul>li>img{
	display: inline-block;
	width: 100%;
}
.eval-c>li{
	width: 100%;
	margin-top: 10px;
	background: white;
	font-size: 16px;
	color: #666666;
	line-height: 25px;
	border-radius: 2px;
}
.name{
	font-weight: bold;
	color: #D54B3A;
	padding-left: 10px;
}
.footer{
	position: fixed;
	bottom: 0px;
	width: 100%;
	height: 42px;
	background: white;
}
.footer>a{
	display: inline-block;
	width: 40px;
	height: 33px;
	background: url(../img/allIcon.png) no-repeat;
	background-size: 250px 250px;
	background-position: -1% 101%;
	margin: 6px 0px 0px 10px;
	position: relative;
}
.footer>a>span{
	position: absolute;
	top: 0px;
	right: 0px;
	background: #ee3749;
	width: 20px;
	height: 20px;
	border-radius: 50%;
	text-align: center;
	line-height: 20px;
	font-size: 14px;
	color: white;
}
.footer>span{
	float: right;
	width: 100px;
	height: 30px;
	margin: 6px 10px;
	font-size: 16px;
	color: white;
	border-radius: 3px;
	text-align: center;
	line-height: 30px;
}
.footer>span:nth-child(2){
	background: #f29004;
}
.footer>span:nth-child(3){
	background: #ee3749;
}



