  text-align: center;
  height: 50px;
  color: #808080;
  font-size: 14px;
  padding-top: 2px;
}
.nav > a > span {
  display: block;
  margin-top: 5px;
}
.nav .router-link-active {
  color: #f29106;
}
.content {
  font-size: 20px;
  padding-bottom: 50px;
}




